@media screen and (max-width: 600px) {
  .root{
    padding-top: 100px;
    flex-direction: column;
    height: 100vh;
  }
  .leftContainer{
    width: 100vw;
    height: 30vh;
    
    margin: 0;
    display: flex;
    justify-content: flex-end;
    
   }
  .rightContainer{
   width: 100vw;
   height: 60vh;
   padding: 0;
   margin: 0;
   display: flex;
   align-items: flex-start;
 }
 .pic{
   
   margin: 0;
   height: 300px;
 }
}